import { Injectable } from '@angular/core';
import { EMPTY, Observable, throwError } from 'rxjs';
import { IViewActionService, RX_LAUNCH_BEHAVIOR } from '@helix/platform/view/api';
import { ILaunchUrlViewActionParams } from './launch-url-view-action-params.interface';
import { RxError } from '@helix/platform/utils';

@Injectable({
  providedIn: 'root'
})
export class RxLaunchUrlViewActionService implements IViewActionService<ILaunchUrlViewActionParams, never> {
  execute(params: ILaunchUrlViewActionParams): Observable<never> {
    if (params.url) {
      window.open(params.url, RX_LAUNCH_BEHAVIOR[params.launchBehavior].target);

      return EMPTY;
    } else {
      return throwError(new RxError('rxLaunchUrlAction: URL is not specified.'));
    }
  }
}
